@import url(https://fonts.googleapis.com/css2?family=Lakki+Reddy&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --fontFamily: "Lakki Reddy", sans-serif;
  --black: rgb(0, 0, 0);
  --darkBlue: rgba(50, 67, 99, 0.74);
  --transparentDarkBlue: rgba(88, 120, 180, 0.377);
  --grey: rgb(54, 52, 52);
  --lightGrey: rgb(201, 197, 197);
  --transparentGrey: rgba(128, 128, 128, 0.514);
  --transition: 0.8s ease;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* SCROLLBAR */

::-webkit-scrollbar {
  width: 10px;
}
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 8px rgba(165, 42, 42, 0.589);
}
::-webkit-scrollbar-thumb {
  background: rgba(211, 158, 14, 0.63);
  border-radius: 15px;
}
::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 166, 0, 0.616);
}

/* CONTAINERS AND COMPONENTS */

.App {
  background-image: url(/ShoppingList/static/media/background2.b9a6d099.jpg);
  background-size: cover;
  height: 100vh;
  font-family: "Lakki Reddy", sans-serif;
  font-family: var(--fontFamily);
  color: rgb(0, 0, 0);
  color: var(--black);
}
.header {
  height: 9vh;
}
.container {
  background-color: rgba(128, 128, 128, 0.514);
  background-color: var(--transparentGrey);
  width: 70%;
  height: 90vh;
  border: 2px solid rgb(0, 0, 0);
  border: 2px solid var(--black);
  border-radius: 10px;
  overflow: auto;
}
h4 {
  text-decoration: underline;
}
.col {
  align-items: center;
}

/* FORM */

.form-control {
  background-color: rgba(50, 67, 99, 0.74);
  background-color: var(--darkBlue);
  border: 1px solid rgb(0, 0, 0);
  border: 1px solid var(--black);
  color: rgb(0, 0, 0);
  color: var(--black);
  transition: 0.8s ease;
  transition: var(--transition);
}
.form-control:focus {
  background-color: rgba(88, 120, 180, 0.377);
  background-color: var(--transparentDarkBlue);
  border-color: rgb(0, 0, 0);
  border-color: var(--black);
  color: rgb(0, 0, 0);
  color: var(--black);
}
.form-control::-webkit-input-placeholder {
  color: rgb(201, 197, 197);
  color: var(--lightGrey);
}
.form-control:-ms-input-placeholder {
  color: rgb(201, 197, 197);
  color: var(--lightGrey);
}
.form-control::-ms-input-placeholder {
  color: rgb(201, 197, 197);
  color: var(--lightGrey);
}
.form-control::placeholder {
  color: rgb(201, 197, 197);
  color: var(--lightGrey);
}
.form-control:-ms-input-placeholder {
  color: rgb(201, 197, 197);
  color: var(--lightGrey);
}
.form-control::-ms-input-placeholder {
  color: rgb(201, 197, 197);
  color: var(--lightGrey);
}
.form-check {
  display: inline;
  margin-left: 8%;
}
input[type="checkbox"] {
  cursor: pointer;
  transition: 0.8s ease;
  transition: var(--transition);
}
input[type="checkbox"]:not(:checked) {
  box-shadow: 0 0 0 3px rgb(54, 52, 52);
  box-shadow: 0 0 0 3px var(--grey);
  transition: 0.8s ease;
  transition: var(--transition);
}
input[type="checkbox"]:checked {
  box-shadow: none;
}

/* BUTTONS */

.btn {
  float: right;
  border-radius: 30%;
  transition: 0.8s ease;
  transition: var(--transition);
}
.btn-danger {
  border-radius: 50%;
  height: 20px;
  width: 20px;
  padding: 0;
  margin-top: 5px;
  margin-left: auto;
}

/* CARD */

.card {
  background-color: rgba(50, 67, 99, 0.74);
  background-color: var(--darkBlue);
  border: 1px solid rgb(0, 0, 0);
  border: 1px solid var(--black);
  border-radius: 5px;
  overflow: hidden;
}
ul {
  width: 95%;
}
small {
  margin-top: auto;
}

@media (max-width: 768px) {
  .header {
    height: 15vh;
  }
  .container {
    width: 90%;
    height: 84vh;
  }
}

